<template>
  <div class="window-title-bar">
    <div class="button-wrapper">
        <img @click="closeApp" src="assets/arrows_remove.png"/>
    </div>
  </div>
</template>

<script>
export default {
    name: 'default',
    data () {
        return {

        }
    },
    props: {
        contents: {
            required: false,
            type: Object
        }
    },
    methods: {
        closeApp () {
            window.close();
        }
    }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
@import '../shared'

.window-title-bar 
    position: absolute
    top: 0
    left: 0
    width: 100vw
    height: 24px
    background: rgb(65,65,65)
    -webkit-app-region: drag
    user-select: none
    overflow: hidden

.button-wrapper img 
    width: 32px
    margin-top: -4px
    margin-left: -4px
    filter: invert(1)
    +transition-fast

.button-wrapper img:hover
    opacity: 0.6
    cursor: pointer

</style>



